export class Serializable {
    fillFromJSON(json: string) {
        var jsonObj = JSON.parse(json).array.forEach(element => {
            console.log(element);
        });
        for (var propName in jsonObj) {
            this[propName] = jsonObj[propName]
        }
    }
}

export class ArticleCard extends Serializable {
     titleText: string;
     articleText : string;
    constructor( _titleText, _articleText)
    {
        super();
        this.articleText = _articleText;
        this.titleText = _titleText;
    }
    GetTitleText(): string { return this.titleText }
    GetArticleText() : string { return this.articleText }
}



