export class Serializable {
    fillFromJSON(json: string) {
        var jsonObj = JSON.parse(json).array.forEach(element => {
            console.log(element);
        });
        for (var propName in jsonObj) {
            this[propName] = jsonObj[propName]
        }
    }
}

export class CardAddButton extends Serializable {
     buttonname: string;
     urlblockname: string;
     _blockId: string;
     _addButtonId: string;
    constructor( _buttonname, _urlblockname, _blockId, _addButtonId )
    {
        super();
        this.buttonname = _buttonname;
        this.urlblockname = _urlblockname
        this._blockId = _blockId;
        this._addButtonId = _addButtonId;
    }
    GetName(): string { return this.buttonname }
    GetUrlBlock(): string{ return this.urlblockname }
    GetBlockId():string {return this._blockId}
    GetAddButtonId():string {return this._addButtonId}
}



