import { Component, ViewEncapsulation,ElementRef, ViewChild } from '@angular/core';
import { BotTrainingService } from './../services/BotTrainingService';
import { GroupsModel } from './../models/GroupsModel';
import { BlocksModel } from './../models/BlocksModel';
import { BlockGroupsModel } from './../models/BlockGroupsModel';
import { Card } from './../models/Card';
import { ImageCard } from './../models/ImageCard';
import { TextCard } from './../models/TextCard';
import { GalleryCard } from './../models/GalleryCard';
import { CardAddButton } from './../models/CardAddButton';
import { ArticleCard } from './../models/ArticleCard';
import {Input} from '@angular/core';
import { Select2OptionData } from 'ng2-select2';
import { __platform_browser_private__ } from '@angular/platform-browser' 
import { Modal} from 'ng2-modal';

declare var jQuery: any;
declare var Messenger: any;
//select2GroupedData: Select2OptionData[] = [];
@Component({
  selector: '[generic]',
  template: require('./generic.component.html'),
  providers:[BotTrainingService],
  encapsulation: ViewEncapsulation.None,
  styles: [require('./generic.component.scss'),'./../forms/elements/elements.style.css']
})
export class Generic {
    @ViewChild('myModal')
    private modalComponent: Modal;
    @ViewChild('yourModal')
    private modalComponent2: Modal;
    selected: any;
    public select2GroupedData: Select2OptionData[] = [];
    @Input() GroupText:string = "";
    public botTrainingService:BotTrainingService;
    private groups: GroupsModel[] = [];
    public base64:string;
    public blockGroupsModel : BlockGroupsModel [] = [];
    public imageCard : ImageCard;
    public textCard : TextCard;
    public galleryCard : GalleryCard;
    public articleCard : ArticleCard;
    public card : Card;
    public cardAddButton : CardAddButton;
    public cardArray : Array<Card>;
    public galleryImageFile : File;
    public blockName: string;
    public addedBlock:BlocksModel;
    ///
    public popBlock:BlocksModel;
    public popBlockName: string = "Block Name";
    public popBlockId:string;
    public galleryCardArray:GalleryCard[];
    public addButtonResponseMessageId:string;
    public AddButtonBlock:string = "";
    public AddButtonName:string = "";
    public AddButtonUrl:string = "";
    public addButtonResponseMessageType: string = "";
    public responseMessageDiff:string;
    public articleText:string = "";
    public ckeditorContent : string[] = [];
    constructor(public _botTrainingService:BotTrainingService, private _rootNode: ElementRef)
    {
        this.galleryCardArray = [];
        this.cardArray = [];
        this.populateGroups();
        this.populateBlocksDropdown();
    }
    onReady(event, i, responseMessageId){
        //alert(i);
        for(var j =0; j<this.cardArray.length ; j++)
        {
            if(this.cardArray[j]._id == responseMessageId){
                this.ckeditorContent[i] = this.cardArray[j].data.articleText;
            }
        }
    }
    onFocus(event, i){
        console.log('Focus');
        console.log(i);
    }
    
    deleteQuickReply(addButtonId, parentId)
    {
        this._botTrainingService.deleteQuickReply(addButtonId,parentId).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            this.blockDetail(this.popBlock._id);
                        }           
                    }
                );
    }
    deleteAddButton(addButtonId, parentId, type)
    {
        this._botTrainingService.deleteAddButton(addButtonId,parentId, type).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                           this.blockDetail(this.popBlock._id);
                        }           
                    }
                );
    }
    deleteResponseMessage(resMesId)
    {
        this._botTrainingService.deleteResponseMessage(resMesId).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            for( var i = 0; i<this.cardArray.length;i++)
                            {
                                var obj = this.cardArray[i];
                                if(obj._id == resMesId)
                                {
                                    this.cardArray.splice(i,1);
                                }
                            }
                            //this.populateGroups();
                        }      
                    }
                );
    }
    populateBlocksDropdown()
    {
         this._botTrainingService.getAllGroups().subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            for(var i = 0; i<this.blockGroupsModel.length;i++)
                                {
                                    if(this.blockGroupsModel[i].blocks.length > 0)
                                    {
                                        for(var j = 0;j<this.blockGroupsModel[i].blocks.length; j++)
                                        {
                                            this.select2GroupedData.push({id:this.blockGroupsModel[i].blocks[j]._id, text:this.blockGroupsModel[i].blocks[j].name});
                                        }
                                        
                                    }
                                }
                            console.log(this.select2GroupedData);
                        }           
                    }
                );
    }
    addButtonPost(val)
    {
        if(val == 1)
        {
            var count = 0;
            var blockname = "";
            for(var i = 0;i < this.select2GroupedData.length; i++)
            {
                if(this.select2GroupedData[i].id == this.AddButtonBlock)
                {
                    blockname = this.select2GroupedData[i].text;
                }
            }
            if(this.responseMessageDiff == 'addButton')
            {
                var cardAddButton = new CardAddButton(this.AddButtonName, blockname, this.AddButtonBlock, "");
                count = jQuery('.addbuttonResultList').length;
            }
            else{
                //Add Quick Reply
                var cardAddButton = new CardAddButton(this.AddButtonName, blockname, this.AddButtonBlock, "");
                count = jQuery('.addQuickReplyList').length;
            }
            if(this.AddButtonBlock != "" && this.AddButtonName != "")
            {
                jQuery('#buttonName').removeClass('modalError');
                jQuery('#simple-select').removeClass('modalError');
                if(this.responseMessageDiff == 'addButton')
                {
                    this._botTrainingService.addAddButton(this.addButtonResponseMessageId, cardAddButton, this.addButtonResponseMessageType, count).subscribe(
                        a=> {
                            if(a.code == 200)
                            {
                                this.AddButtonName = "";
                                this.AddButtonBlock = "";
                                this.blockDetail(this.popBlock._id);
                                this.modalComponent.close();
                            }
                        }
                    );
                }
                else{
                    this._botTrainingService.addQuickReply(this.addButtonResponseMessageId, cardAddButton, this.addButtonResponseMessageType, count).subscribe(
                        a=> {
                            if(a.code == 200)
                            {
                                this.AddButtonName = "";
                                this.AddButtonBlock = "";
                                this.blockDetail(this.popBlock._id);
                                this.modalComponent2.close();
                            }
                        }
                    );
                }
            }
            else{
                jQuery('#buttonName').addClass('modalError');
                jQuery('#simple-select').addClass('modalError');
            }
        }
        else{
            var cardAddButton = new CardAddButton(this.AddButtonName, this.AddButtonUrl, "", "");
            if(this.AddButtonUrl != "" && this.AddButtonName != "")
            {
                jQuery('#buttonName').removeClass('modalError');
                jQuery('#urlContentData').removeClass('modalError');
                this._botTrainingService.addAddButton(this.addButtonResponseMessageId, cardAddButton, this.addButtonResponseMessageType, jQuery('.addbuttonResultList').length).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            this.AddButtonName = "";
                            this.AddButtonUrl = "";
                            this.blockDetail(this.popBlock._id);
                            this.modalComponent.close();
                        }
                    }
                );
            }
            else{
                jQuery('#buttonName').addClass('modalError');
                jQuery('#urlContentData').addClass('modalError');
            }
        }
    }
    addButton(responseMessageId, type, diff)
    {
        this.responseMessageDiff = diff;
        if(diff == 'addButton')
        {
            //jQuery('#urlBtn').show();
            //jQuery('#blockBtn').addClass('modalliactive');
            jQuery('#blockContent').css('display','block');
            jQuery('#quickReplyContent').css('display','none');
            //jQuery('#blockContent').show();
            //jQuery('#quickReplyContent').hide();
            /*jQuery('#blockContent').addClass('modalContentActive');
            jQuery('#blockContent').removeClass('modalContentDeActive');
            
            jQuery('#quickReplyContent').removeClass('modalContentActive');
            jQuery('#quickReplyContent').addClass('modalContentDeActive');*/
        }
        else{
            jQuery('#blockContent').css('display','none');
            jQuery('#quickReplyContent').css('display','block');
            //jQuery('#blockContent').hide();
            //jQuery('#quickReplyContent').show();
            //jQuery('#urlBtn').hide();
            //jQuery('#blockBtn').removeClass('modalliactive');
            /*jQuery('#blockContent').removeClass('modalContentActive');
            jQuery('#blockContent').addClass('modalContentDeActive');

            jQuery('#quickReplyContent').addClass('modalContentActive');
            jQuery('#quickReplyContent').removeClass('modalContentDeActive');*/
        }
        this.addButtonResponseMessageType = type;
        this.addButtonResponseMessageId = responseMessageId;
    }
    getSelect2DefaultList(): Select2OptionData[] {
        console.log(this.select2GroupedData);
        return this.select2GroupedData;
    }
    modalChangeTab(val)
    {
        if(val == 2){
            jQuery('#blockBtn').removeClass('modalliactive');
            jQuery('#urlBtn').addClass('modalliactive');
            jQuery('#blockContent').removeClass('modalContentActive');
            jQuery('#blockContent').addClass('modalContentDeActive');
            jQuery('#urlContent').addClass('modalContentActive');
            jQuery('#urlContent').removeClass('modalContentDeActive');
        }
        else{
            jQuery('#blockBtn').addClass('modalliactive');
            jQuery('#urlBtn').removeClass('modalliactive');
            jQuery('#blockContent').addClass('modalContentActive');
            jQuery('#blockContent').removeClass('modalContentDeActive');
            jQuery('#urlContent').removeClass('modalContentActive');
            jQuery('#urlContent').addClass('modalContentDeActive');
        }
    }
    //Card Functions
    onChange(event, responseMessageId)
    {
        //console.log(event);
    }
    onBlur(event, responseMessageId, i)
    {
        console.log(i);
        console.log(this.ckeditorContent[i]);
        this._botTrainingService.updateArticleText(responseMessageId, this.ckeditorContent[i]).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            console.log(a.message);
                        }           
                    }
                );
    }
    updateTitleArticleCard(responseMessageId)
    {
        this._botTrainingService.updateTitleText(responseMessageId,jQuery('#articleCardTitle'+responseMessageId).val(),0,'article').subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            console.log(a.message);
                        }           
                    }
                );
    }
    updateTitleTextCard(responseMessageId)
    {
        this._botTrainingService.updateTitleText(responseMessageId,jQuery('#textCardTitle'+responseMessageId).val(),0,'text').subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            console.log(a.message);
                        }           
                    }
                );
    }
    updateTitleGalleryCard(responseMessageId, indexId)
    {
        this._botTrainingService.updateTitleText(responseMessageId,jQuery('#galleryCardTitle'+indexId).val(),indexId,'gallery').subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            console.log(a.message);
                        }           
                    }
                );
    }
    updateDescriptionGalleryCard(responseMessageId, indexId)
    {
        this._botTrainingService.updateDescriptionText(responseMessageId,jQuery('#galleryCardDescription'+indexId).val(),indexId).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            console.log(a.message);
                        }           
                    }
                );
    }

    updateUrlGalleryCard(responseMessageId, indexId)
    {
        this._botTrainingService.updateUrlText(responseMessageId,jQuery('#galleryCardUrl'+indexId).val(),indexId).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            console.log(a.message);
                        }           
                    }
                );
    }
    openGalleryImage(Id){
        jQuery('#galleryImage'+Id).click();
    }
    openSingleImage(Id){
        jQuery('#singleImage'+Id).click();
    }
    populateGalleryImage(event, responseMessageId, blockId, indexId){
        let file = event.target.files[0];
        console.log(indexId);
        this._botTrainingService.getPictureUrl(file, responseMessageId, "gallery", indexId).subscribe(
                a=> {
                        if(a.code == 200)
                        {
                            this.blockDetail(blockId);
                        }
                    }
                    
                );
    }
    populateSingleImage(event, responseMessageId, blockId){
        let file = event.target.files[0];
        this._botTrainingService.getPictureUrl(file, responseMessageId, "image", -1).subscribe(
                a=> {
                        if(a.code == 200)
                        {
                            this.blockDetail(blockId);
                        }
                    }
                    
                );
    }
    ///
    addBlock(Id)
    {
        jQuery('.addBlock'+Id).hide();
        jQuery('.saveBlock'+Id).show();
        
    }
    addGalleryCard(resMesId, blockId)
    {
        console.log(resMesId);
        this.galleryCard = new GalleryCard("", "", "", "", [], jQuery('.galleryColumns'+resMesId).length);
        //this.card = new Card("gallery", this.galleryCard);
            this._botTrainingService.addGalleryCard(this.galleryCard,resMesId).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            this.blockDetail(blockId);
                        }           
                    }
                );
    }
    saveBlock(Id)
    {
        jQuery('#blockText'+Id).css('border','1px solid rgba(0, 0, 0, 0.15);');
        if(this.blockName != "" && this.blockName !== undefined)
        {
            this._botTrainingService.addBlocks(this.blockName, 'Generic',Id).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            jQuery('.addBlock'+Id).show();
                            jQuery('.saveBlock'+Id).hide();
                            this.blockName = "";
                            this.addedBlock = a.data;
                            for( var i = 0; i<this.blockGroupsModel.length;i++)
                            {
                                
                                    if(this.blockGroupsModel[i].group._id == Id)
                                    {
                                        this.blockGroupsModel[i].blocks.push(this.addedBlock);
                                        break;
                                    }
                                
                            }
                        }           
                    }
                );
        }
        else
        {
            jQuery('#blockText'+Id).css('border','1px solid red');
        }
    }
    deleteBlock(Id)
    {
        jQuery('.rmdiv').hide();
        this._botTrainingService.deleteBlock(Id)
            .subscribe(
                a=> {
                        if(a.code == 200)
                        {
                            for( var i = 0; i<this.blockGroupsModel.length;i++)
                            {
                                for(var j = 0;j<this.blockGroupsModel[i].blocks.length;j++)
                                {
                                    if(this.blockGroupsModel[i].blocks[j]._id == Id)
                                    {
                                        this.blockGroupsModel[i].blocks.splice(j,1);
                                        break;
                                    }
                                }
                            }
                            //this.populateGroups();
                        }
                }
            );
    }
    blockDetail(Id)
    {
        this.ckeditorContent = [];
        jQuery('.blockbtns').removeClass("blockactive");
        jQuery('#block'+Id).addClass("blockactive");
        this.populateResponseMessages(Id);
    }
    populateResponseMessages(Id)
    {
        this._botTrainingService.getAllResponseMessages(Id).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            console.log(a);
                            this.cardArray = [];
                            this.popBlock = a.data.block;
                            this.cardArray = a.data.responseMessages;
                            this.popBlockName = this.popBlock.name;
                            jQuery('.rmdiv').show();
                        }           
                    }
                );
    }
    addCard(val)
    {
        if(val == 1)
        {
            //Gallery
            this.galleryCard = new GalleryCard("", "", "", "", [], 1);
            this.card = new Card("gallery", this.galleryCard);
            var galleryCardArray = [];
            galleryCardArray.push(this.galleryCard);
            console.log(this.galleryCard);
            this._botTrainingService.addResponseMessage(galleryCardArray,"gallery",this.popBlock._id).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            this.cardArray.push(a.data);
                        }           
                    }
                );       
        }
        else if(val == 2)
        {
            //Text
            this.textCard = new TextCard("", [], []);
            this.card = new Card("text", this.textCard);
            this._botTrainingService.addResponseMessage(this.textCard,"text",this.popBlock._id).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            this.cardArray.push(a.data);
                        }           
                    }
                );
        }
        else if(val == 3)
        {
            //Image
            this.imageCard = new ImageCard("", []);
            this.card = new Card("image", this.imageCard);
            this._botTrainingService.addResponseMessage(this.imageCard,"image",this.popBlock._id).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            this.cardArray.push(a.data);
                        }           
                    }
                );
        }
        else if(val == 4)
        {
            //Article
            this.articleCard = new ArticleCard("", "");
            this.card = new Card("image", this.articleCard);
            this._botTrainingService.addResponseMessage(this.articleCard,"article",this.popBlock._id).subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            this.cardArray.push(a.data);
                        }           
                    }
                );
        }
    }
    
  sortOptions: Object = {
    placeholder: 'list-group-item list-group-item-placeholder',
    forcePlaceholderSize: true
  };
  nest1Options: Object = { group: 1 };
  nest2Options: Object = { group: 1 };
  
  ngOnInit(): void {

      jQuery('.nav-tabs').on('shown.bs.tab', 'a', (e) => {
      if (e.relatedTarget) {
        jQuery(e.relatedTarget).removeClass('active');
      }
    });
      var oldIndex;
      jQuery('#popover2').popover();
      
    jQuery('.GroupBtn').show();
    jQuery('.GroupForm').hide();
    jQuery( '.list-group-sortable' ).sortable(this.sortOptions);
    jQuery( '.list-group-sortable2' ).sortable(this.sortOptions);
    jQuery( '#nestable1' ).nestable(this.nest1Options);
    jQuery( '#nestable2' ).nestable(this.nest2Options);
    jQuery( ".list-group-sortable" ).sortable({
                start: function( event, ui ) {
                    oldIndex = ui.item.index() + 1;
                    console.log(ui.item[0]);
                    jQuery(this).attr('data-previndex', ui.item[0].id);
                },
                update: function(e, ui) {
                    // gets the new and old index then removes the temporary attribute
                    var newIndex = ui.item.index() + 1;
                    console.log("newIndex " + newIndex);
                    var groupId = jQuery(this).attr('data-previndex');
                    console.log(groupId);
                    jQuery.post("http://aprilappserver.azurewebsites.net/groups/setOrderOfGroups",
                    {
                    oldIndex: oldIndex,
                    newIndex: newIndex,
                    groupId:groupId
                    },
                    function(data,status){
                        console.log(data);
                    });
                    jQuery(this).removeAttr('data-previndex');
                }
            });

            //sorting of response messages
            jQuery( ".list-group-sortable2" ).sortable({
                start: function( event, ui ) {
                    oldIndex = ui.item.index() + 1;
                    console.log(oldIndex);
                    var array = ui.item[0].childNodes;
                    var id;
                    array.forEach(function(entry) {
                        if(entry.id !== undefined)
                        {
                            id = entry.id;
                        }
                    });
                    jQuery(this).attr('data-previndex', id);
                },
                update: function(e, ui) {
                    // gets the new and old index then removes the temporary attribute
                    var newIndex = ui.item.index() + 1;
                    console.log("newIndex " + newIndex);
                    var groupId = jQuery(this).attr('data-previndex');
                    console.log(groupId);
                    jQuery.post("http://aprilappserver.azurewebsites.net/responsemessage/sortingOfResponseMessages",
                    {
                    oldIndex: oldIndex,
                    newIndex: newIndex,
                    groupId:groupId
                    },
                    function(data,status){
                        console.log(data);
                    });
                    jQuery(this).removeAttr('data-previndex');
                }
            });
  }
  toggleDivs(val)
  {
      if( val == 1 )
      {
          jQuery('.GroupBtn').fadeOut("slow");
          jQuery('.GroupForm').fadeIn("slow");
      }
      else{
          jQuery('#emptyGroup').hide();
          if(this.GroupText != "")
          {
              this._botTrainingService.addGroup(this.GroupText, 'Generic').subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            this.populateGroups();
                            this.GroupText = "";
                        }           
                    }
                );
          }
          else{
              jQuery('#emptyGroup').fadeIn("slow");
          }
      }
  }
  populateGroups()
  {
      this._botTrainingService.getAllGroups()
            .subscribe(
                a=> {
                        if(a.code == 200)
                        {
                            this.blockGroupsModel = a.data;
                            this.blockGroupsModel.sort(function(a, b) {
                                return a.group.order - b.group.order;
                            });
                            if(this.blockGroupsModel.length != 0)
                            {
                                for(var i = 0; i<this.blockGroupsModel.length;i++)
                                {
                                    if(this.blockGroupsModel[i].blocks.length > 0)
                                    {
                                        this.popBlock = this.blockGroupsModel[i].blocks[0];
                                        this.popBlockId = this.popBlock._id;
                                        break;
                                    }
                                }
                            }
                        }
                }
            );
  }
  deleteGroup(val)
  {
      this._botTrainingService.deleteGroup(val)
            .subscribe(
                a=> {
                        if(a.code == 200)
                        {
                            this.populateGroups();
                        }
                }
            );
  }
  refreshTextBox()
  {
      this.GroupText = "";
  }
}




