import {CardAddButton} from './CardAddButton';
export class Serializable {
    fillFromJSON(json: string) {
        var jsonObj = JSON.parse(json).array.forEach(element => {
            console.log(element);
        });
        for (var propName in jsonObj) {
            this[propName] = jsonObj[propName]
        }
    }
}

export class ImageCard extends Serializable {
     pictureUrl: string;
     quickReplyButton : CardAddButton[];
    constructor( _pictureUrl, _quickReplyButton)
    {
        super();
        this.pictureUrl = _pictureUrl;
        this.quickReplyButton = _quickReplyButton;
    }
    GetPictureUrl(): string { return this.pictureUrl }
    GetCardQuickReplyButton() : CardAddButton[] { return this.quickReplyButton }
}



