import {CardAddButton} from './CardAddButton';
export class Serializable {
    fillFromJSON(json: string) {
        var jsonObj = JSON.parse(json).array.forEach(element => {
            console.log(element);
        });
        for (var propName in jsonObj) {
            this[propName] = jsonObj[propName]
        }
    }
}

export class GalleryCard extends Serializable {
     pictureUrl: string;
     title: string;
     description: string;
     url: string;
     cardAddButton : CardAddButton[];
     indexId:number;
    constructor( _pictureUrl, _title, _description, _url, _cardAddButton, _indexId)
    {
        super();
        this.pictureUrl = _pictureUrl;
        this.title = _title;
        this.description = _description;
        this.url = _url;
        this.cardAddButton = _cardAddButton;
        this.indexId = _indexId;
    }
    GetPictureUrl(): string { return this.pictureUrl }
    GetTitle(): string{ return this.title }
    GetDescription(): string { return this.description }
    GetUrl(): string{ return this.url }
    GetCardAddButton(): CardAddButton[]{ return this.cardAddButton }
    GetIndexId(): number{ return this.indexId }
}



