import 'messenger/build/js/messenger.js';
import 'jquery-ui/ui/sortable.js';
import 'jquery.nestable/jquery.nestable.js';

import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { AlertModule, TooltipModule } from 'ng2-bootstrap/ng2-bootstrap';
import { ButtonsModule, DropdownModule } from 'ng2-bootstrap/ng2-bootstrap';
import { TabsModule, AccordionModule } from 'ng2-bootstrap/ng2-bootstrap';
import { ModalModule } from 'ng2-modal';
import { TabsAccordion } from './../ui-elements/tabs-accordion/tabs-accordion.component';
import { Generic } from './generic.component';
import { WidgetModule } from '../layout/widget/widget.module';
import { Select2Module } from 'ng2-select2';
import { CKEditorModule } from 'ng2-ckeditor';

export const routes = [
  { path: '', component: Generic, pathMatch: 'full' }
  
];

@NgModule({
  declarations: [
    // Components / Directives/ Pipes
    Generic,
    TabsAccordion
  ],
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(routes),
    AlertModule,
    WidgetModule,
    TooltipModule,
    ModalModule,
    ButtonsModule,
    DropdownModule,
    TabsModule,
    AccordionModule,
    Select2Module,
    CKEditorModule
  ]
})
export default class UiElementsModule {
  static routes = routes;
}



